import React, { useEffect } from "react"
import "./AdminCarList.scss"
import Paginator from "../../common/Paginator/Paginator"
import { connect } from "react-redux"
import {
  requestCarsPage,
  setCurrentPage,
  requestCarsTotal,
} from "../../../store/cars-table-reducer"
import Loader from "../../common/Loader/Loader"
import {
  getAdminTableColors,
  getAdminCarNames,
  getAdminCarTypes,
} from "../../common/helpers/Helpers"
import AdminCarListFilter from "./AdminCarListFilter"
import { useState } from "react"

let firstOption = []
let secondOption = []

const AdminCarList = ({
  carsTotal,
  carsPerPage,
  isFetching,
  pageSize,
  currentPage,
  carsCount,
  requestCarsPage,
  setCurrentPage,
  requestCarsTotal,
}) => {
  const [filters, setFilters] = useState({ model: "", type: "" })
  useEffect(() => {
    requestCarsPage(currentPage, pageSize, filters)
    requestCarsTotal()
  }, [currentPage, pageSize, requestCarsPage, requestCarsTotal, filters])

  firstOption = [
    { key: "–í—Å–µ –º–æ–¥–µ–ª–∏", value: "" },
    ...getAdminCarNames(carsTotal),
  ]
  secondOption = [
    { key: "–í—Å–µ —Ç–∏–ø—ã", value: "" },
    ...getAdminCarTypes(carsTotal),
  ]
  const onFiltersSubmit = ({ model, type }) => {
    setFilters({ model, type })
    setCurrentPage(0)
  }
  const clearFilters = () => {
    setFilters({ model: "", type: "" })
    setCurrentPage(0)
  }

  return (
    <div className="admin__car-list car-list">
      <div className="content__title">–°–ø–∏—Å–æ–∫ –∞–≤—Ç–æ</div>
      <div className="car-list__content-wrapper">
        <div className="car-list__content">
          <AdminCarListFilter
            filters={filters}
            firstOption={firstOption}
            secondOption={secondOption}
            onSubmit={onFiltersSubmit}
            clearFilters={clearFilters}
          />
          <div className="car-list__table">
            {isFetching ? (
              <Loader admin={true} />
            ) : carsPerPage.length ? (
              <table>
                <thead>
                  <tr>
                    <th scope="col">–ú–æ–¥–µ–ª—å</th>
                    <th scope="col">–¢–∏–ø</th>
                    <th scope="col">–¶–µ–Ω–∞</th>
                    <th scope="col">–¶–≤–µ—Ç–∞</th>
                  </tr>
                </thead>
                <tbody>
                  {carsPerPage.map((car) => (
                    <tr key={car.id}>
                      <td data-label="–ú–æ–¥–µ–ª—å">{car.name}</td>
                      <td data-label="–¢–∏–ø">{car.categoryId.name}</td>
                      <td data-label="–¶–µ–Ω–∞">{`${car.priceMin} - ${car.priceMax} ‚ÇΩ`}</td>
                      <td data-label="–¶–≤–µ—Ç–∞">
                        {getAdminTableColors(car.colors)}
                      </td>
                    </tr>
                  ))}
                </tbody>
              </table>
            ) : (
              <div className="no-data-found">–ù–∏—á–µ–≥–æ –Ω–µ –Ω–∞–π–¥–µ–Ω–æ</div>
            )}
          </div>
          <div className="car-list__footer">
            <Paginator
              itemsCount={carsCount}
              pageSize={pageSize}
              onPageChange={({ selected }) => setCurrentPage(selected)}
            />
          </div>
        </div>
      </div>
    </div>
  )
}

const mapStateToProps = (state) => ({
  carsTotal: state.carsTable.carsTotal,
  carsPerPage: state.carsTable.carsPerPage,
  isFetching: state.carsTable.isFetching,
  pageSize: state.carsTable.pageSize,
  currentPage: state.carsTable.currentPage,
  carsCount: state.carsTable.carsCount,
})
export default connect(mapStateToProps, {
  requestCarsPage,
  setCurrentPage,
  requestCarsTotal,
})(AdminCarList)
